import Layout from '../components/MyLayout.js'
import Link from 'next/link'
import Head from 'next/head'


const PostLink = (props) => (
  <li>
    <Link as={`/p/${props.id}`} href={`/post?title=${props.title}`}>
      <a>{props.title}</a>
    </Link>
  </li>
)

export default () => (
    <div>
        <Layout>
            <h3>Share what you have brought</h3>
            <div className="container">
            <form>
                <div className="form-group">
                    <label>Flipkart link</label>
                    <input type="text" className="form-control" placeholder="Paste the flipkart link"/>
                    <p className="help-block">Go to flipkat website browse the product you brought copy the website address paste it above.</p>
                </div>
                <div className="form-group">
                    <label>Caption</label>
                    <input type="text" className="form-control" placeholder="Write something..."/>
                </div>
                <div className="form-group">
                    <label>Photo</label>
                    <input type="file" />
                    <p className="help-block">Upload photo (Optional).</p>
                </div>
                <button type="submit" className="btn btn-default">Submit</button>
                </form>
            </div>
        </Layout>
    </div>
)


